/* ==========================================================================
   Recipe General
   ========================================================================== */
/* ==========================================================================
   Recipe Actions Bar
   ========================================================================== */
.recipe-actions {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  border-top: 1px solid #dddddd;
  padding: 8px 0;
}

.recipe-actions > * {
  display: inline-block;
  margin-left: 16px;
}

.recipe-actions > *:first-child {
  margin-left: 0;
}

/* ==========================================================================
   Recipe Ingredients
   ========================================================================== */
.recipe-ingredients {
  margin: 0 0 16px;
  padding: 0;
  list-style-type: disc;
  list-style-position: inside;
}

@media screen and (min-width: 1281px) {
  .recipe-ingredients {
    columns: 2;
  }
}

.recipe-ingredients .recipe-ingredient {
  margin-bottom: 4px;
}

.recipe-ingredients .recipe-ingredient-note {
  margin-left: 4px;
}

.recipe-ingredients .recipe-ingredient-note::before {
  content: "(";
}

.recipe-ingredients .recipe-ingredient-note::after {
  content: ")";
}

/* ==========================================================================
   Recipe Meta
   ========================================================================== */
/* ==========================================================================
   Recipe Phases
   ========================================================================== */
.recipe-phase {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 0 0 16px;
}

.recipe-phase > *:last-child {
  margin-bottom: 0;
}

/* ==========================================================================
   Recipe Steps
   ========================================================================== */
.recipe-steps {
  margin: 0 0 16px;
  padding: 0;
  list-style-position: inside;
}

.recipe-steps .recipe-step {
  margin-bottom: 16px;
}

.recipe-steps .recipe-step:last-child {
  margin-bottom: 0;
}

/* ==========================================================================
   Recipe Times
   ========================================================================== */
.recipe-times {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 8px 0;
  display: block;
  line-height: 1.25;
}

.recipe-times li {
  display: inline-block;
  width: 30%;
  margin-right: 4%;
  border: 0;
}

.recipe-times li:last-child {
  margin-right: 0;
}

.recipe-times li strong {
  display: block;
}

.recipe-list {
  padding: 0 0 16px;
  margin: 0 0 16px;
  list-style: none;
}

.recipe-list .recipe {
  border-bottom: 1px solid #dddddd;
  padding-bottom: 16px;
  margin-bottom: 16px;
}

.recipe-list .recipe meta[itemprop="url"] {
  clear: both;
  display: block;
}

.recipe-list .recipe:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: 0;
}

.recipe-list .recipe .recipe-image {
  float: left;
  max-width: 150px;
  margin-right: 16px;
  margin-top: .125em;
}

.recipe-list .recipe .recipe-title {
  clear: none;
  margin: 0 0 .5em;
}

.recipe-list .recipe .recipe-meta {
  display: block;
  font-size: 15px;
  line-height: 1;
}

.recipe-list .recipe .recipe-meta > * {
  margin-right: 10px;
}

.recipe-list .recipe .recipe-meta i.dashicons {
  font-size: 1.375em;
  line-height: .75em;
  width: 1em;
  height: 1em;
}

.recipe-list .recipe .recipe-meta .recipe-total-time span {
  text-indent: -9999em;
  display: inline-block;
}

.recipe-list .recipe .recipe-description {
  margin: 0;
}

/* ==========================================================================
   Single Recipe
   ========================================================================== */
.single-recipe .recipe-wrapper .entry-summary h1, .single-recipe .recipe-wrapper .entry-summary h2, .single-recipe .recipe-wrapper .entry-summary h3 {
  margin-top: .5em;
  margin-bottom: .7em;
}

.single-recipe .recipe-wrapper .entry-summary h4, .single-recipe .recipe-wrapper .entry-summary h5, .single-recipe .recipe-wrapper .entry-summary h6 {
  margin-top: 1.2em;
  margin-bottom: .8em;
}

.single-recipe .recipe-wrapper .images {
  margin-bottom: 16px;
}

.single-recipe .recipe-wrapper .images a {
  border: 0;
  text-decoration: none;
  display: inline-block;
  outline: 0;
}

.single-recipe .recipe-wrapper .images img {
  display: inline-block;
}

.single-recipe .recipe-wrapper .recipe-meta {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 0 0 16px;
}

.single-recipe .recipe-wrapper .recipe-description {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 0 0 16px;
}

.single-recipe .recipe-wrapper .recipe-description > *:last-child {
  margin-bottom: 0;
}

.recipe-shortcode .images {
  margin-bottom: 16px;
}

.recipe-shortcode .images a {
  border: 0;
  text-decoration: none;
  display: inline-block;
  outline: 0;
}

.recipe-shortcode .images img {
  display: inline-block;
}

.recipe-shortcode.full-recipe .entry-summary {
  padding: 0;
}

.recipe-shortcode.full-recipe .recipe-description {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 0 0 16px;
}

.recipe-shortcode.full-recipe .recipe-description > *:last-child {
  margin-bottom: 0;
}

.recipe-shortcode.full-recipe .recipe-meta {
  border-bottom: 1px solid #dddddd;
  margin: 0 0 16px;
  padding: 0 0 16px;
}

